"use client";

import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { useEffect, useState } from "react";
import { Exam } from "@/types";
import { CreateExamDialog } from "@/components/create-exam-dialog";
import { useRouter } from "next/navigation";
import * as gtag from "@/utils/gtag";
import axios from "axios";

const API_URL = "http://localhost:3000/"; // Replace with your API URL

export default function Home() {
  const [exams, setExams] = useState<Exam[]>([]);
  const router = useRouter();

  const fetchExams = async () => {
    try {
      const res = await axios.get(`${API_URL}/exams`);
      setExams(res.data);
    } catch (err) {
      console.error("Failed to fetch exams:", err);
    }
  };

  useEffect(() => {
    fetchExams();
  }, []);

  const handleExamClick = (examId: string) => {
    const selectedExam = exams.find((exam) => exam.id === examId);
    if (selectedExam) {
      gtag.event({
        action: "click_exam_card",
        category: "navigation",
        label: selectedExam.name,
        value: 1,
      });
    }
    router.push(`/exam/${examId}`);
  };

  const handleCreateExam = async (newExam: Exam) => {
    try {
      const res = await axios.post(`${API_URL}/exams`, newExam);
      setExams((prev) => [...prev, res.data]);
    } catch (err) {
      console.error("Failed to create exam:", err);
    }
  };

  const handleDeleteExam = async (id: string) => {
    try {
      await axios.delete(`${API_URL}/exams/${id}`);
      setExams((prev) => prev.filter((exam) => exam.id !== id));
    } catch (err) {
      console.error("Failed to delete exam:", err);
    }
  };

  const handleEditExam = async (updatedExam: Exam) => {
    try {
      const res = await axios.put(
        `${API_URL}/exams/${updatedExam.id}`,
        updatedExam
      );
      setExams((prev) =>
        prev.map((exam) => (exam.id === updatedExam.id ? res.data : exam))
      );
    } catch (err) {
      console.error("Failed to update exam:", err);
    }
  };

  return (
    <div className="flex flex-col items-center justify-start min-h-screen py-10">
      <h1 className="text-4xl font-bold mb-4 text-primary">Exam AI Grader</h1>
      <section className="w-full max-w-4xl">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-2xl font-semibold text-foreground">Exams</h2>
          <CreateExamDialog onCreateExam={handleCreateExam} />
        </div>
        <div className="grid gap-4 grid-cols-1 md:grid-cols-2 lg:grid-cols-3">
          {exams.map((exam) => (
            <Card
              key={exam.id}
              className="relative hover:shadow-md transition-shadow"
            >
              <div
                onClick={() => handleExamClick(exam.id)}
                className="cursor-pointer"
              >
                <CardHeader>
                  <CardTitle>{exam.name}</CardTitle>
                  <CardDescription>{exam.description}</CardDescription>
                </CardHeader>
                <CardContent>
                  <p>Tasks: {exam.tasks?.length ?? 0}</p>
                </CardContent>
              </div>
              <div className="absolute top-2 right-2 flex gap-2">
                <Button
                  variant="destructive"
                  size="sm"
                  onClick={(e) => {
                    e.stopPropagation();
                    handleDeleteExam(exam.id);
                  }}
                >
                  Delete
                </Button>
                <Button
                  size="sm"
                  onClick={(e) => {
                    e.stopPropagation();
                    const name = prompt("New exam name:", exam.name);
                    const description = prompt(
                      "New description:",
                      exam.description
                    );
                    if (name && description) {
                      handleEditExam({ ...exam, name, description });
                    }
                  }}
                >
                  Edit
                </Button>
              </div>
            </Card>
          ))}
        </div>
      </section>
    </div>
  );
}
